import { DirectiveOptions, DirectiveFunction } from 'vue';
import debounce from "lodash.debounce";

const click: DirectiveOptions = {
    bind(el: any, binding: any, vnode: any) {
        const arg = binding.arg
        const emit = debounce(() => {
            binding.value(arg)
        },
            1500,
            { leading: true, }// 立即执行
        )

        el.onclick = function () {
            emit();
        }
    }
}
export default click;
